
html {
  height: 100%;
  margin: 0;
  padding: 0;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
.kiwi-wrap {
  font-size: 90%;
  line-height: 1.6em;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  overflow: hidden;
}
.kiwi-wrap--monospace {
  font-family: Consolas, monaco, monospace;
  font-size: 80%;
}
.kiwi-workspace {
  position: relative;
  margin-left: 220px;
  left: 0;
  display: block;
  height: 100%;
  transition: left 0.2s, margin-left 0.2s;
}
.kiwi-workspace::before {
  position: absolute;
  content: '';
  right: 0;
  left: 0;
  top: 0;
  height: 7px;
  z-index: 0;
}
/* When the statebrowser opens as a draw, darken the workspace */
.kiwi-workspace::after {
  position: fixed;
  top: 0;
  right: 0;
  content: '';
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.5s;
  will-change: opacity;
}
.kiwi-workspace-background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
}
.kiwi-statebrowser {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  bottom: 0;
  transition: left 0.2s;
  z-index: 1;
}
.kiwi-container {
  position: absolute;
  top: 0;
  bottom: 40px;
  width: 100%;
}
.kiwi-mediaviewer {
  max-height: 70%;
  overflow: auto;
}
.kiwi-controlinput {
  position: absolute;
  bottom: 0;
  height: 40px;
  width: 100%;
  z-index: 2;
}
/* Small screen will cause the statebrowser to act as a drawer */
@media screen and (max-width: 769px) {
.kiwi-workspace {
    left: 0;
    margin-left: 0;
}
.kiwi-statebrowser {
    left: -200px;
}
.kiwi-wrap--statebrowser-drawopen .kiwi-statebrowser {
    left: 0;
}
.kiwi-wrap--statebrowser-drawopen .kiwi-workspace {
    left: 75%;
    width: 80%;
}
.kiwi-wrap--statebrowser-drawopen .kiwi-workspace::after {
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: 10;
}
}
h1,
