    opacity: 0.75;
}

.u-form {
    text-align: left;
}

.u-form label {
    display: block;
    line-height: 20px;
    margin: 0 0 10px 0;
}

.u-form label span {
    margin-right: 1em;
    display: inline-block;
    line-height: 25px;
    width: auto;
    font-weight: 500;
    text-align: left;
    max-width: none;
}

.u-form label.u-form-block span {
    width: auto;
    display: inline-block;
}

/* Style all form inputs */

.u-form input[type='checkbox'],
.u-form input[type='radio'] {
    float: left;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    display: inline-block;
    position: relative;
    border-radius: 1px;
    cursor: pointer;
    margin-left: 0;
    margin-right: 5px;
}

.u-form input[type='checkbox']::after,
.u-form input[type='radio']::after {
    position: absolute;
    content: '';
    left: 1px;
    top: 1px;
    width: 14px;
    height: 14px;
    z-index: 10;
    transition: all 0.3s;
    opacity: 0;
}

.u-form input[type='checkbox']:checked::after,
.u-form input[type='radio']:checked::after {
    position: absolute;
    content: '';
    left: 1px;
    top: 1px;
    width: 14px;
    height: 14px;
    z-index: 10;
    transition: all 0.3s;
    opacity: 1;
}

.u-form input[type="radio"] {
    float: right;
}

.u-form .u-input,
.u-form input[type='text'],
.u-form input[type='password'],
.u-form input[type='email'],
.u-form textarea {
    font-size: 0.9em;
    clear: both;
    width: 100%;
    height: 40px;
    padding: 3px 5px;
    line-height: 40px;
    box-sizing: border-box;
    border-radius: 1px;
    min-height: none;
    overflow-x: hidden;
    overflow-y: auto;
    max-width: none;
}

.u-checkbox-wrapper {
    position: relative;
    padding-left: 30px;
}

.u-checkbox-wrapper input[type="checkbox"],
.u-checkbox-wrapper input[type="radio"] {
    position: absolute;
    margin: 0;
    top: 2px;
    left: 0;
}

.u-checkbox-wrapper label {
    margin-right: 0;
}

.u-form .u-submit {
    margin: 2em auto;
    display: block;
}

